html, body, #root {
  margin: 0;
  height: 100%;
  font-family: Tahoma,Arial,Verdana,Helvetica,"Bitstream Vera Sans",sans-serif;
  font-size: 15px; }

.loading {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%;
  width: 100%; }
  .loading .spinner {
    padding-bottom: 40px;
    padding-left: 40px;
    height: 100px;
    width: 100px;
    font-size: 100px;
    animation: spin 4s linear infinite;
    color: gray; }

@keyframes spin {
  from {
    transform: rotate(0deg); }
  to {
    transform: rotate(360deg); } }

.dark {
  background-color: #1c2938;
  color: #eee0e0; }

.light {
  background-color: #ffffff;
  color: #7e7070; }

.displayModes {
  display: flex;
  justify-content: right; }

.feeds {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  justify-content: space-around;
  width: -moz-fit-content; }

.feed {
  border: 1px solid lightgray;
  margin: 2px;
  padding: 5px; }
  .feed img {
    height: 16px; }
  .feed .title {
    margin: 0 0 5px 0;
    display: flex;
    justify-content: space-between; }

.dark a {
  color: #eee0e0; }

.light a {
  color: #7e7070; }

a {
  cursor: pointer;
  font-style: normal;
  font-weight: normal;
  font-size: 13px;
  line-height: 0.8em;
  font-size-adjust: none;
  font-stretch: normal;
  -x-system-font: none;
  font-feature-settings: normal;
  font-language-override: normal;
  font-kerning: auto;
  font-synthesis: weight style;
  font-variant-alternates: normal;
  font-variant-caps: normal;
  font-variant-east-asian: normal;
  font-variant-ligatures: normal;
  font-variant-numeric: normal;
  font-variant-position: normal;
  text-decoration: none; }
  a:hover {
    color: #5aadbb; }

.date {
  font-size: 12px; }

.text-badge {
  cursor: pointer;
  display: inline-block;
  position: relative;
  padding: 0px 3px 0px;
  min-width: 12px;
  margin-left: 12px;
  border-radius: 3px;
  white-space: nowrap;
  overflow: hidden;
  background-color: #7e7070; }
  .text-badge a {
    font-size: 14px;
    font-family: inherit; }
  .text-badge.open {
    background-color: #518d6a; }
  .text-badge.refresh {
    background-color: steelblue; }
  .text-badge.close {
    background-color: #f19c4c; }
  .text-badge a {
    color: white; }
    .text-badge a:hover {
      color: #5aadbb; }
